\t\t\tdetail:[],\n          infoWxc:{}\n\t\t\t\t}\n\t\t\t},\n\t\t\tmethods: {\n\t\t\t\tgo: page.go,\n\t\t\t\tfirst: page.first,\n\t\t\t\tend: page.end,\n\t\t\t\tprev: page.prev,\n\t\t\t\tnext: page.next,\n\t\t\t\tshowpage: page.showpage,\n\t\t\t\tpaging(e) {\n\t\t\t\t\tthis.page = e;\n\t\t\t\t\tthis.init()\n\t\t\t\t},\n\t\t\t\tinit() {\n\t\t\t\t\tthis.$axios.post('admin/ShopMateriel/lbList', {\n\t\t\t\t\t\t\ttoken: this.token,\n              page: this.page,\n              status:0\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.then(res => {\n\t\t\t\t\t\t\tif(res.data.code == 1) {\n\t\t\t\t\t\t\t\tthis.list = res.data.data.list;\n\t\t\t\t\t\t\t\tthis.showpage(res.data.data);\n\t\t\t\t\t\t\t\tthis.pageCount = res.data.data.rows\n\t\t\t\t\t\t\t}else{\n                this.list=[]\n\t\t\t\t\t\t\t\tlayer.msg(res.data.msg)\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.catch(err => console.log(err))\n\t\t\t\t},\n\t\t\t\tshowreason: function(id) {\n\t\t\t\t\tthis.id = id;\n\t\t\t\t\tthis.$axios.post('admin/ShopDownList/dowReason', {\n\t\t\t\t\t\t\ttoken: this.token,\n\t\t\t\t\t\t\tid: id\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.then(res => {\n\t\t\t\t\t\t\t\n\t\t\t\t\t\t\tlayer.open({\n\t\t\t\t\t\t\t\ttype: 0,\n\t\t\t\t\t\t\t\tarea: ['40%', '30%'],\n\t\t\t\t\t\t\t\ttitle: ['关停理由', 'height:60px;background-color:#98D1F9;color:white;text-align:center;line-height:60px;font-size:20px;'],\n\t\t\t\t\t\t\t\tshadeClose: true,\n\t\t\t\t\t\t\t\tshade: 0.8,\n\t\t\t\t\t\t\t\tcontent: res.data.data\n\t\t\t\t\t\t\t})\n\t\t\t\t\t\t}).catch(err => console.log(err))\n\t\t\t\t},\n\t\t\t   async adopt(id) {\n\t\t\t\t\ttry{\n                        const res = await this.$axios.post('admin/ShopMateriel/detail',{token:this.token,id:id})\n                        if(res.data.code==1){\n                            this.detailsList=res.data.data\n\t\t\t\t\t\t\t              this.detail = res.data.data.list.detail\n                            this.infoWxc = res.data.data.list\n                        }else{\n                             this.detailsList=[]\n                        }\n                    }catch(err){\n                         throw(err)\n                    }\n                },\n                async Sure(){\n\t\t\t\t\tlet activeSupplier = this.chooseSupplier || 0\n                    try{\n                       const res = await this.$axios.post('admin/ShopMateriel/adopt',{token:this.token,id:this.eachData.id,phone:this.eachData.phone,gid:activeSupplier})\n\t\t\t\t\t   if(res.data.code==1){\n                           this.detailsDialogVisible=false,\n                           this.$message({message:res.data.msg,type:'success'})\n                           this.init()\n                       }else{\n                           this.$message.error(res.data.msg)\n                       }\n                    } catch(err){\n                        throw(err)\n                    }\n                },\n\n\t\t\t\tcancel() {\n\t\t\t\t\tlayer.close(this.layerIndex);\n\t\t\t\t},\n\t\t\t\tpass() {\n\t\t\t\t\tif(!this.reason) {\n\t\t\t\t\t\tlayer.msg('请输入开通理由！');\n\t\t\t\t\t\treturn;\n\t\t\t\t\t}\n\t\t\t\t\tthis.$axios.post('admin/ShopDownList/openAgain', {\n\t\t\t\t\t\t\ttoken: this.token,\n\t\t\t\t\t\t\tid: this.id,\n\t\t\t\t\t\t\treason: this.reason\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.then(res => {\n\t\t\t\t\t\t\t\n\t\t\t\t\t\t\tif(res.data.code == 1) {\n\t\t\t\t\t\t\t\tlayer.msg(res.data.msg, {\n\t\t\t\t\t\t\t\t\ttime: 500\n\t\t\t\t\t\t\t\t}, function() {\n\t\t\t\t\t\t\t\t\tlayer.closeAll();\n\t\t\t\t\t\t\t\t});\n\t\t\t\t\t\t\t\tthis.init();\n\t\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\t\tlayer.closeAll();\n\t\t\t\t\t\t\t\tthis.$alert('开通失败', '提示', {\n\t\t\t\t\t\t\t\t\ttype: 'error'\n\t\t\t\t\t\t\t\t});\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.catch(err => alert(err))\n\t\t\t\t}\n\t\t\t},\n\t\t\tcreated() {\n\t\t\t\tthis.init();\n\t\t\t},\n\t\t\tmounted() {\n\t\t\t\tvar id = this.$route.query.id;\n\t\t\t\tthis.curId = id;\n\t\t\t\tthis.$axios.post('admin/Auth/erAuth', {\n\t\t\t\t\t\ttoken: window.sessionStorage.getItem('bbytoken'),\n\t\t\t\t\t\tid: id\n\t\t\t\t\t})\n\t\t\t\t\t.then(res => {\n\t\t\t\t\t\t\n\t\t\t\t\t\tif(res.data.code == 1) {\n\t\t\t\t\t\t\tvar arr = res.data.data;\n\t\t\t\t\t\t\tfor(var i = 0; i < arr.length; i++) {\n\t\t\t\t\t\t\t\tif(arr[i].son) {\n\t\t\t\t\t\t\t\t\tif(arr[i].name == '致敬老兵') {\n\t\t\t\t\t\t\t\t\t\tthis.seCurId = arr[i].id;\n\t\t\t\t\t\t\t\t\t\tthis.threeAuthList = arr[i].son;\n\t\t\t\t\t\t\t\t\t\t\n\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\tfor(var j = 0; j < arr[i].son.length; j++) {\n\t\t\t\t\t\t\t\t\t\tif(arr[i].action != arr[i].son[j].action) {\n\t\t\t\t\t\t\t\t\t\t\tarr[i].action = arr[i].son[0].action;\n\t\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\t\tif(arr[i].son[j].name == '未审核' && arr[i].name == '物料审核') {\n\t\t\t\t\t\t\t\t\t\t\tthis.thCurId = arr[i].son[j].id;\n\t\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\tthis.authList = arr;\n\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\tthis.$alert(res.data.msg, '提示', {\n\t\t\t\t\t\t\t\ttype: 'error'\n\t\t\t\t\t\t\t});\n\t\t\t\t\t\t}\n\t\t\t\t\t})\n\t\t\t\t\t.catch(err => {\n\t\t\t\t\t\talert(err);\n\t\t\t\t\t})\n\t\t\t},\n\t\t\twatch: {\n\t\t\t\tindex() {\n\t\t\t\t\tthis.init();\n\t\t\t\t}\n\t\t\t}\n\t}\n</script>\n<style scoped>\n    .container{\n\t\t/*margin-top: 50px;*/\n\t}\n\t.laybox {\n\t\ttext-align: center;\n\t\tpadding: 50px 68px 50px 36px;\n\t\tfont-size: 16px;\n\t\toverflow: hidden;\n\t}\n\n\t.laybox textarea {\n\t\toutline: none;\n\t\tresize: none;\n\t    width: 80%;\n\t    flex-flow: 1;\n\t\theight: 250px;\n\t\tborder: 1px dashed #04BBFC;\n\t\tmargin-bottom: 60px;\n\n\t\tborder-radius: 15px;\n\t}\n\t.laybox>p{\n\t\tdisplay: flex;\n\t\tflex-flow: 1;\n\n\t}\n\n\tdiv.bot button {\n\t\twidth: 110px;\n\t\theight: 36px;\n\t\tborder-radius: 18px;\n\t\tbox-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\n\t\tborder: none;\n\t\tcolor: white;\n\t}\n\n\tbutton.reject {\n\t\tbackground: rgba(237, 81, 55, 1);\n\t\tmargin-right: 30%;\n\